<template lang="html">
    <div id="case">
        <Head></Head>
        <div class="container case">
            <div class="row">
                <div v-for="caseList in caseLists" class="col-md-3 caseList" v-on:click="CasetuF($event)">
                    <div class="con-shadow-before" v-bind:data-id="caseList.id" >
                        <img v-bind:src="caseList.brand_logo" alt="" class="">
                        <p class="hide">{{caseList.description}}</p>
                    </div>
                </div>
            </div>
        </div>
        <canvasBg></canvasBg>
        <greyDetails :CaseDetailId="CaseDetailId"  :CaseDetailImg="CaseDetailImg" :CaseDetailTT="CaseDetailTT" :CaseDetailP="CaseDetailP"></greyDetails>

    </div>

</template>

<script>
import canvasBg from './components/canvasBg'
import Head from './components/Head'
import greyDetails from './components/greyDetails'
export default {
    name: 'Case',
    components:{
        canvasBg,
        Head,
        greyDetails
    },
    data:function(){
        return{
            caseLists:'',
            CaseDetailImg:'',
            CaseDetailTT:'',
            CaseDetailP:'',
            CaseDetailId:''

        }
    },
    created(){
        this.caseListsF()
        // this.CasetuF()
    },
    methods:{
        caseListsF:function(){
             this.$http.get('http://adwww.ldcywh.com/index.php/index/Api/brand_case')
             .then((res)=>{
                 console.log(res.data);
                this.caseLists=res.data.data.brand_case;
                $(".con-shadow-before").mouseover(function() {
                    $(this).addClass("con-shadow-after");
                    $(this).find("p").fadeIn();
                });

                $(".con-shadow-before").mouseout(function() {
                    $(this).removeClass("con-shadow-after");
                    $(this).find("p").hide();
                });
            })
        },
        CasetuF:function(){ //案例详情
            $(".caseList").on("click",function(){
                $(".greyDetails").fadeIn(1400);
                $("body").css("overflow","hidden");
                $("html,body").animate({
                       scrollTop:0
                   }, 1000)
            })
            var data = $(event.currentTarget);
            var dataid = data.find("div").attr('data-id');
            console.log(dataid)
            this.CaseDetailId = dataid;
            this.$http.get('http://adwww.ldcywh.com/index.php/index/Api/getCaseDetail',{params: {id:dataid}})
            .then( (res) => {
                console.log(res.data)
                this.CaseDetailImg = res.data.data.brand_case.detail_pic;
                this.CaseDetailTT = res.data.data.brand_case.title;
                this.CaseDetailP = res.data.data.brand_case.description;
                // console.log(this.CaseDetailP)

            })
        }
     },


}
$(function(){
    setInterval(function(){
        $(".con-shadow-before").mouseover(function() {
            console.log("12");
            $(this).addClass("con-shadow-after");
            $(this).find("p").fadeIn();
        });

        $(".con-shadow-before").mouseout(function() {
            $(this).removeClass("con-shadow-after");
            $(this).find("p").hide();
        });
    },200)

})
</script>

<style lang="css">
body,p,h1,h2,h3,h4,span{font-family: "PingFang SC", "Hiragino Sans GB", "Heiti SC", "Microsoft YaHei", "WenQuanYi Micro Hei";}
html{font-family: "PingFang SC", "Hiragino Sans GB", "Heiti SC", "Microsoft YaHei", "WenQuanYi Micro Hei";}
.hide{display: none;}
.anlie-ym{color: #fff !important;}
.case{margin-top: 80px;}
.case img{width: 100%;}
.case .col-md-3{
    position: relative;
    margin: 20px 0;
}
.case p{
    position: absolute;
    bottom: 0;
    margin: auto;
    width: 100%;
    background-color: rgba(0,0,0,.2);
    line-height: 30px;
    padding: 0 10px;
    color: #fff;
}
.con-shadow-after{
box-shadow:0 0 20px #d9d9d9;
transform: translateY(-5px);
-webkit-animation: danyuan_men_point .2s linear alternate forwards;
-o-animation: danyuan_men_point .2s linear alternate forwards;
animation: danyuan_men_point .2s linear alternate forwards;
}
@-webkit-keyframes danyuan_men_point {
    0% { transform:translateY(0px);}
    100%{ transform:translateY(-5px);}
}
@-o-keyframes danyuan_men_point {
    0% { transform:translateY(0px);}
    100%{ transform:translateY(-5px);}
}
@keyframes danyuan_men_point {
    0% { transform:translateY(0px);}
    100%{ transform:translateY(-5px);}
}
</style>



